.newsForm textarea {
    width: 100%;
    height: 100px;
    margin: 0 auto;
    border-radius: 10px;
    border: 1px solid rgb(45, 80, 165);
    padding: 10px;
    outline: none;
    resize: none;
}
.newsForm input {
    width: 300px;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid rgb(45, 80, 165);
    margin-bottom: 5px;
    outline: none;
}
